const $siteList = $(".siteList");
const $lastLi = $siteList.find("li.last");
//Ê≥®ÊÑè‰ªégetItem‰∏≠ÊãøÂá∫xÊó∂Ë¶ÅÂä†ÂçïÂºïÂè∑ÔºåÂõ†‰∏∫xÊ≠§Êó∂ÊòØÊï∞ÁªÑ
const x = localStorage.getItem('x')//ÊääÂèòÊàêÂ≠óÁ¨¶‰∏≤Âêç‰∏∫xÁöÑhashmap‰ªélocalStorage‰∏≠ÊãøÂá∫Êù•
const xObject = JSON.parse(x)//Â∞ÜhashmapÈáçÊñ∞Âèò‰∏∫ÂØπË±°
//Â∞ÜhashmapÁ≠â‰∫éxObject ||(ÊàñËÄÖ) Á≠â‰∫éÂ≠òÂÖ•ÁöÑÊï∞ÁªÑ
const hashMap = xObject || [
  {
    logo: "https://developer.mozilla.org/favicon-192x192.png",
    url: "https://developer.mozilla.org/zh-CN/",
    link: "MDN"
  },
  { logo: "src/Caniuse.jpg", 
    url: "https://caniuse.com/",
    link: "Caniuse"
  },
];

const render= ()=>{
  $siteList.find('li:not(.last)').remove()//Êää‰πãÂâçÁöÑliÈÉΩÊâæÂà∞Èô§‰∫ÜÊúÄÂêé‰∏Ä‰∏™ÔºåÁÑ∂ÂêéÂà†Èô§
  hashMap.forEach(node => {
    const $li = $(`<li>
    <a href="${node.url}">
      <div class="site">
        <div class="logo"><img src=${node.logo}/></div>
        <div class="link">${node.link}</div>
      </div>
    </a>
  </li>
    `).insertBefore($lastLi);
  });
}
//Ë∞ÉÁî®render
render();

$(".addButton").on("click", () => {
  let url = window.prompt("ËØ∑ÈóÆ‰Ω†Ë¶ÅÊ∑ªÂä†ÁöÑÁΩëÂùÄÊòØÂï•Ôºü");
  let urlName = window.prompt("ËØ∑ÈóÆ‰Ω†Ë¶ÅÊ∑ªÂä†ÁöÑÁΩëÂùÄÂêçÁß∞");
  console.log(url);
  if (url.indexOf("http") != 0) {
    url = "https://" + url;
  }
  console.log(url);
  hashMap.push({
    logo:url[0],
    url:url,
    link:urlName,
    logoType:'text',
  })
  //Ë∞ÉÁî®render
   render();
});

//ÁõëÂê¨Áî®Êà∑ÂÖ≥Èó≠ÊµèËßàÂô®ÁöÑAPI
window.onbeforeunload = ()=>{
  const string = JSON.stringify(hashMap)//ÊääÂØπË±°ÂèòÊàêÂ≠óÁ¨¶‰∏≤
  localStorage.setItem('x',string)//ÊääÂèòÊàêÂ≠óÁ¨¶‰∏≤ÁöÑhashmapÂ≠òÂÖ•localStorage‰∏≠Âêç‰∏∫x
}